html, body, #root, .App, .full-height {
  min-height: 100%;
  height: auto;
}

html, body {
  background-color: #000;
  color: #fff;

  --gold: #ffc400;
  --gold-dark: #c79400;

  font-family: Arial, Helvetica, sans-serif;

  -webkit-font-smoothing: antialiased;
  font-smooth: always;
  moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  -moz-font-feature-settings: “liga” on;

  font-size: 16px;

  ::selection {
    background-color: var(--gold-dark);
    color: #000;
  }
}

.full-height {
  display: grid;
  justify-content: center;
  align-items: center;
  background-image: url('./assets/second-offset.jpg');
  background-size: cover;
  background-position: center;
  position: relative;
  min-height: 100vh;

  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: #000;
    opacity: 0.7;
    z-index: 0;
  }
}

.logo {
  max-width: 300px;
  margin: 0 auto;
  path, polygon, rect {
    fill: #fff;
  }
}

.main-content {
  max-width: 632px;
  padding: 16px;
  margin: auto;
  position: relative;
  z-index: 1;

  display: grid;
  grid-gap: 40px;
}

h1 {
  font-size: 4.688rem;
  line-height: 4.688rem;
  font-weight: bold;
  @media (max-width: 500px) {
    font-size: 3rem;
    line-height: 3rem;
  }

}

p {
  font-size: 2rem;
  line-height: 2.375rem;
  text-align: center;
  @media (max-width: 500px) {
    font-size: 1.658rem;
    line-height: 1.658rem;
  }
}

.sub {
  font-weight: bold;
}


a {
  display: block;
  color: var(--gold);
  text-decoration: none;
  position: relative;
  font-size: 1rem;
  line-height: 1rem;
  padding: 10px 0;
  width: fit-content;
  overflow: hidden;

  &:after {
    content: '';
    content: "";
    border: 1px solid var(--gold-dark);
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateX(-110%);
    transition: all 0.75s cubic-bezier(0.075, 0.82, 0.165, 1);
  }

  &:visited {
    color: var(--gold);
  }

  &:hover, &:active, &:focus {
    color: var(--gold-dark);
    outline: none;
    &:after {
      transform: translateX(1%);
    }
  }
}

.contacts {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  width: fit-content;
  grid-gap: 40px;
  margin: 0 auto;

  svg {
    width: 25px;
  }
}